<template>
    <view class="give_away_page">
        <!-- tabbar -->
        <cu-custom><block slot="content">上传资料</block></cu-custom>
        <form @submit="formSubmit">
            <view class="flex justify-center align-start input_value_box">
                <view class="input_value_title">运输实际损耗</view>
                <view class="flex justify-start align-center input_box">
                    <input class="withdraw_money" type="digit" placeholder="输入实际损耗" name="loss_weight" />
                </view>
            </view>
            <view class="upload_box bg-white">
                <view class="cu-bar">
                    <view class="action title">
                        上传收据
                    </view>
                </view>
                <view class="cu-form-group">
                    <view class="grid col-4 grid-square flex-sub">
                        <view class="bg-img" v-for="(item,index) in imgList" :key="index" @tap="ViewImage" :data-url="imgList[index]">
                        <image :src="imgList[index]" mode="aspectFill"></image>
                            <view class="cu-tag bg-red" @tap.stop="DelImg" :data-index="index">
                                <text class='cuIcon-close'></text>
                            </view>
                        </view>
                        <view class="solids" @tap="ChooseImage" v-if="imgList.length<1">
                            <text class='cuIcon-cameraadd'></text>
                        </view>
                    </view>
                </view>
            </view>
            <!-- 支付按钮 -->
            <view class="flex justify-center align-center btn_box">
                <button class="cu-btn recharge_btn" form-type="submit" :disabled="buttonType">提交</button>
            </view>
        </form>
        <!-- loading弹框组件 -->
		<popLinkDialog></popLinkDialog>
		<popLoading></popLoading>
		<popDialog></popDialog>
		<tipModal></tipModal>
		<!-- 登录 -->
		<login></login>
    </view>
</template>

<script>
import { upload_image_api,confirmTransactedGrabsGoods_api } from '@/api/index.js';
import { appData } from '@/utils/config.js';
import { mapState, mapGetters, mapActions, mapMutations } from 'vuex';
import storage from '@/utils/storage.js';
import { identity_format } from '@/utils/filters.js';
import checkForm from '@/utils/checkFormData.js';
	let that = null;
export default {
    data() {
        return {
            appBaseData:null,//全局配置信息
            userInfo: {},
            order_id: '',
            imgList: [],
            transport_pic_id: '',// 收据id
            buttonType: false,
        }
    },
    watch: {
        '$store.getters.base_buttonType': function(val) {
            that.buttonType = val;
        },
    },
    methods: {
        ...mapMutations([
            'modal',
            'user',
            'base'
        ]),
        /**图片上传
         * @description: 
         * @param {*}
         * @return {*}
         */
        ChooseImage() {
            uni.chooseImage({
                count: 1, //默认9
                sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
                sourceType: ['album'], //从相册选择
                success: (res) => {
                    if (this.imgList.length != 0) {
                        this.imgList = this.imgList.concat(res.tempFilePaths)
                    } else {
                        this.imgList = res.tempFilePaths
                    };
                    let params = res.tempFilePaths[0];
                    upload_image_api(params,that).then(res=>{
                        that.transport_pic_id = res.data.file_id;
                    }).catch(err=>{
                        console.log(err);
                    })
                }
            });
        },
        /**图片预览
         * @description: 
         * @param {*} e
         * @return {*}
         */
        ViewImage(e) {
            uni.previewImage({
                urls: this.imgList,
                current: e.currentTarget.dataset.url
            });
        },
        /**删除图片
         * @description: 
         * @param {*} e
         * @return {*}
         */
        DelImg(e) {
            uni.showModal({
                title: '提示',
                content: '确定要删除图片吗？',
                cancelText: '取消',
                confirmText: '是的',
                success: res => {
                    if (res.confirm) {
                        this.imgList.splice(e.currentTarget.dataset.index, 1);
                        that.transport_pic_id = '';
                    }
                }
            })
        },
        /**
         * @description: 提交表单
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        formSubmit(e) {
             //定义表单规则
            let rule = [
                {name:"loss_weight", checkType : "notnull", checkRule:"",  errorMsg:"请输入损耗"},
                {name:"transport_pic_id", checkType : "notnull", checkRule:"",  errorMsg:"请上传收据图片"},
            ];
            //进行表单检查
            let formData = e.detail.value;
            formData.grabs_order_id = that.order_id;
            formData.transport_pic_id = that.transport_pic_id;
            let checkRes = checkForm.check(formData, rule);
            if(!checkRes){
                that.modal({name:'popTipModal',value:{type: true,tipTitle:'表单错误',tipContent:checkForm.error}});
                return false;
            }
            that.modal({name:'popLoading',value:true});
            that.base({name:'buttonType',value:true});
            confirmTransactedGrabsGoods_api(formData,that).then(res=>{
                that.modal({name:'popLoading',value:false});
                that.base({name:'buttonType',value:false});
                that.base({name:'refreshPage',value:true});
                that.modal({name:'popLinkDialog',value:{type:true,tipTitle:'提示',tipContent:'提交成功',linkUrl:1}});
            }).catch(err=>{
                console.log(err);
                that.modal({name:'popLoading',value:false});
                that.base({name:'buttonType',value:false});
            })
        },
    },
    // 页面生命周期
    onLoad: function (options) {
        that = this;
        if (!options.order_id || options.order_id == ''|| options.order_id == 'undefined') {
            that.modal({name:'popLinkDialog',value:{type:true,tipTitle:'提示',tipContent:'该订单不存在',linkUrl:1}});
            return false;
        }
        that.order_id = options.order_id;
    }
}
</script>
<style lang="scss" scoped>
    @import "./index.scss";
</style>